#tercomment_list_item {
  background-color: white;
}
#tercomment_list_item:last-child {
  border-bottom: 0.1rem #F3F6F9 solid;
}
#tercomment_list_item:last-child .item::after {
  display: none;
}
#tercomment_list_item .item {
  padding: 0.15rem 0.1rem;
  width: 100%;
}
#tercomment_list_item .item img {
  width: 0.35rem;
  height: 0.35rem;
  border-radius: 50%;
  margin-right: 0.1rem;
}
#tercomment_list_item .item .rightCont {
  width: calc(100% - .45rem);
}
#tercomment_list_item .item .content {
  font-size: 0.14rem;
  margin-top: 0.07rem;
  word-break: break-word;
  line-height: 1.6;
}
#tercomment_list_item .item .userName {
  line-height: 1.2;
  position: relative;
  font-size: 0.14rem;
  padding-top: 0.05rem;
}
#tercomment_list_item .item .timeCont {
  font-size: 0.12rem;
}
#tercomment_list_item .item .timeCont .blue {
  color: #3672ED;
}
#tercomment_list_item .item .timeCont .courseName {
  padding-right: 0.03rem;
  line-height: 1.2;
}
#tercomment_list_item .item .timeCont > span {
  padding-top: 0.08rem;
  display: inline-block;
  vertical-align: middle;
}



