html,
body {
  height: 100%;
  margin: 0;
  padding: 0;
  overflow: hidden;
  color: #7A809B;
}
html li,
body li {
  list-style: none;
}
html a,
body a {
  text-decoration: none;
  outline: none;
  color: #7A809B;
}
html .am-whitespace,
body .am-whitespace {
  background-color: #F3F6F9;
}
html .am-whitespace.am-whitespace-md,
body .am-whitespace.am-whitespace-md {
  height: 0.1rem;
}
html .borderGray,
body .borderGray {
  border-bottom: 0.1rem #F3F6F9 solid;
}
#root {
  height: 100%;
  overflow: hidden;
}
#root > div {
  height: 100%;
  overflow: hidden;
}
#root .content_window {
  height: calc(100% - .4rem);
  width: 100%;
  overflow-y: hidden;
  background-color: #F3F6F9;
}
#root .content_window_all {
  height: 100%;
  overflow-y: hidden;
}
#root .tab {
  background-color: #fff;
  display: -ms-flexbox;
  display: flex;
  height: 0.4rem;
  text-align: center;
  -webkit-box-shadow: 0 1px 2px rgba(0, 0, 0, 0.5);
          box-shadow: 0 1px 2px rgba(0, 0, 0, 0.5);
  position: absolute;
  width: 100%;
  bottom: 0;
  left: 0;
  z-index: 50;
}
#root .tab .tab-item {
  -ms-flex: 1 1;
      flex: 1 1;
  font-size: 0.1rem;
}
#root .tab .tab-item .nav-link {
  display: block;
  color: inherit;
  text-decoration: none;
}
#root .tab .tab-item i {
  display: block;
  width: 0.17rem;
  height: 0.18rem;
  margin: 0.05rem auto 0.04rem;
}
#root .tab .tab-item.tab_course i {
  width: 0.2rem;
  height: 0.18rem;
  background: url("../static/img/tab_course.png") no-repeat;
  background-size: 100%;
}
#root .tab .tab-item.tab_course.active {
  color: #3672ED;
}
#root .tab .tab-item.tab_course.active i {
  background: url("../static/img/tab_course_active.png") no-repeat;
  background-size: 100%;
}
#root .tab .tab-item.tab_sort i {
  background: url("../static/img/tab_sort.png") no-repeat;
  background-size: 100%;
}
#root .tab .tab-item.tab_sort.active {
  color: #3672ED;
}
#root .tab .tab-item.tab_sort.active i {
  background: url("../static/img/tab_sort_active.png") no-repeat;
  background-size: 100%;
}
#root .tab .tab-item.tab_myCurriculum i {
  background: url("../static/img/tab_myCurriculum.png") no-repeat;
  background-size: 100%;
}
#root .tab .tab-item.tab_myCurriculum.active {
  color: #3672ED;
}
#root .tab .tab-item.tab_myCurriculum.active i {
  background: url("../static/img/tab_myCurriculum_active.png") no-repeat;
  background-size: 100%;
}
#root .tab .tab-item.tab_person i {
  background: url("../static/img/tab_person.png") no-repeat;
  background-size: 100%;
}
#root .tab .tab-item.tab_person.active {
  color: #3672ED;
}
#root .tab .tab-item.tab_person.active i {
  background: url("../static/img/tab_person_active.png") no-repeat;
  background-size: 100%;
}
#root .translate-enter {
  -webkit-transform: translate3d(100%, 0, 0);
          transform: translate3d(100%, 0, 0);
}
#root .translate-enter.translate-enter-active {
  -webkit-transition: -webkit-transform 0.3s;
  transition: -webkit-transform 0.3s;
  -o-transition: transform 0.3s;
  transition: transform 0.3s;
  transition: transform 0.3s, -webkit-transform 0.3s;
  -webkit-transform: translate3d(0, 0, 0);
          transform: translate3d(0, 0, 0);
}
@media screen and (min-width: 768px) {
  #root #home-category .slider-container {
    height: 1.47rem;
  }
  #root #detil .detil_content .imgDiv {
    height: 2rem;
  }
}



