#pay_list .textDec {
  padding: 0.12rem;
  text-align: center;
  font-size: 0.12rem;
  color: #7A809B;
}
#pay_list .payItem {
  background-color: white;
  padding: 0.15rem 0.1rem;
  font-size: 0.14rem;
  color: #2A3350;
  border-top: 1px #EDEFF9 solid;
}
#pay_list .payItem:first-child {
  border-top: 0;
}
#pay_list .payItem .title_color {
  margin-bottom: 0.15rem;
  font-size: 0.14rem;
}
#pay_list .payItem .cont div {
  position: relative;
  line-height: 0.3rem;
  padding-left: 0.39rem;
}
#pay_list .payItem .cont div:first-child {
  background: url("../../static/img/icon_weixin.png") no-repeat left center;
  background-size: 0.29rem;
  margin-bottom: 0.15rem;
}
#pay_list .payItem .cont div:last-child {
  background: url("../../static/img/icon_alipay.png") no-repeat left center;
  background-size: 0.29rem;
}
#pay_list .payItem .cont div::after {
  content: '';
  position: absolute;
  right: 0.1rem;
  width: 0.2rem;
  height: 100%;
  background: url("../../static/img/icon_payRadio.png") no-repeat right center;
  background-size: 0.2rem;
}
#pay_list .payItem .cont div.active::after {
  content: '';
  position: absolute;
  right: 0.1rem;
  width: 0.2rem;
  height: 100%;
  background: url("../../static/img/icon_payRadioActive.png") no-repeat right center;
  background-size: 0.2rem;
}
#pay_list .payItem .cont.paySao div:first-child {
  background: url("../../static/img/icon_weixinSao.png") no-repeat left center;
  background-size: 0.29rem;
  margin-bottom: 0.15rem;
}
#pay_list .payItem .cont.paySao div:last-child {
  background: url("../../static/img/icon_alipaySao.png") no-repeat left center;
  background-size: 0.29rem;
}
#pay_list .payItem .cont.elsePay div {
  background: url("../../static/img/icon_overMoney.png") no-repeat left center;
  background-size: 0.29rem;
  margin-bottom: 0;
}



