.myCourse {
  height: 100%;
}
.myCourse .myCourseList_header {
  background-color: #fff;
  text-align: center;
  line-height: 0.44rem;
  font-size: 0.18rem;
  padding: 0 0.2rem;
  position: relative;
}
.myCourse .myCourseList_header > i {
  position: absolute;
  right: 0.2rem;
  color: #7A809B;
}
.myCourse .myCourseList_content {
  height: calc(100% - .57rem);
  background-color: #F5F6FA;
}



