#filter {
  position: absolute;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: 111;
  /*创建动画*/
}
#filter .shade {
  height: 100%;
  background-color: rgba(0, 0, 0, 0.3);
}
#filter .filter_panel {
  background-color: #fff;
  width: 2.47rem;
  height: 100%;
  position: absolute;
  right: -2.7rem;
  top: 0;
  padding-top: 0.2rem;
}
#filter .filter_panel_enter {
  -webkit-animation-name: moveTest;
          animation-name: moveTest;
  -webkit-animation-duration: 0.15s;
          animation-duration: 0.15s;
  -webkit-animation-timing-function: linear;
          animation-timing-function: linear;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  /*设置动画的播放状态  paused:暂停   running:播放*/
  -webkit-animation-play-state: running;
          animation-play-state: running;
}
#filter .margin15 {
  margin: 0 0.15rem;
}
#filter .p10 {
  padding: 0 0.1rem;
}
#filter .title_color {
  font-size: 0.18rem;
  padding-bottom: 0.08rem;
}
#filter .title {
  color: #4C4C4C;
  font-size: 0.12rem;
  padding: 0.15rem 0.05rem 0;
}
#filter .cont {
  height: calc(100% - .72rem);
  padding-bottom: 0.1rem;
}
#filter .blueBtn {
  position: absolute;
  bottom: 0;
  left: 0;
}
#filter .content span {
  background-color: #F2F2F2;
  font-size: 0.13rem;
  padding: 0.1rem 0.1rem 0.08rem;
  margin: 0.1rem 0.05rem 0;
  display: inline-block;
  line-height: 1;
}
#filter .content span.active {
  background-color: #E4E6F7;
  color: #3672ED;
}
#filter .content span.disable {
  color: #9E9E9E;
}
@-webkit-keyframes moveTest {
  from {
    -webkit-transform: translate(0, 0);
            transform: translate(0, 0);
  }
  to {
    -webkit-transform: translate(-2.7rem, 0);
            transform: translate(-2.7rem, 0);
  }
}
@keyframes moveTest {
  from {
    -webkit-transform: translate(0, 0);
            transform: translate(0, 0);
  }
  to {
    -webkit-transform: translate(-2.7rem, 0);
            transform: translate(-2.7rem, 0);
  }
}
#filter .filter_panel_leave {
  -webkit-animation-name: moveLeave;
          animation-name: moveLeave;
  -webkit-animation-duration: 0.15s;
          animation-duration: 0.15s;
  -webkit-animation-timing-function: linear;
          animation-timing-function: linear;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  /*设置动画的播放状态  paused:暂停   running:播放*/
  -webkit-animation-play-state: running;
          animation-play-state: running;
}
@-webkit-keyframes moveLeave {
  from {
    -webkit-transform: translate(-2.7rem, 0);
            transform: translate(-2.7rem, 0);
  }
  to {
    -webkit-transform: translate(0, 0);
            transform: translate(0, 0);
  }
}
@keyframes moveLeave {
  from {
    -webkit-transform: translate(-2.7rem, 0);
            transform: translate(-2.7rem, 0);
  }
  to {
    -webkit-transform: translate(0, 0);
            transform: translate(0, 0);
  }
}



