.set_up {
  height: 100%;
}
.set_up .set_up_content {
  height: 100%;
  padding-top: 0.68rem;
}
.set_up .set_up_content .am-list-content {
  color: #2A3350;
  font-size: 0.14rem;
  padding-top: 0.15rem;
  padding-bottom: 0.15rem;
}
.set_up .set_up_content .am-list-item {
  padding: 0 0.15rem;
}
.set_up .set_up_content .am-list-item .am-list-line {
  padding-right: 0;
}
.set_up .set_up_content .quite {
  padding: 0.12rem;
  text-align: center;
  background-color: white;
  color: #4285F4;
  margin-top: 0.2rem;
  font-size: 0.14rem;
}
@media screen and (max-width: 720px) {
  .set_up .am-switch .checkbox {
    width: 38px;
    height: 20px;
  }
  .set_up .am-switch .checkbox:after {
    height: 18px;
    width: 18px;
    border-radius: 18px;
    left: -1.5px;
    top: 1px;
  }
  .set_up .am-switch .checkbox:before {
    height: 18px;
    width: 32px;
    border-radius: 18px;
    left: 4.5px;
    top: 1px;
  }
}



