.Bind_Phone {
  height: 100%;
  background: #F3F6F9;
}
.Bind_Phone .Bind_content {
  height: 100%;
  padding: 0.74rem 0.15rem 0;
}
.Bind_Phone .textDec {
  font-size: 0.13rem;
  text-align: center;
  color: #AFB4CC;
  margin-bottom: 0.15rem;
}
.Bind_Phone .inputDiv {
  height: 0.45rem;
  margin-bottom: 0.2rem;
  position: relative;
  padding-left: 0.5rem;
}
.Bind_Phone .inputDiv.phoneDiv {
  background: white url("../../static/img/icon_phone.png") no-repeat 0.16rem center;
  background-size: 0.14rem;
}
.Bind_Phone .inputDiv input {
  height: 0.3rem;
  width: calc(100% - .1rem);
  margin: 0.075rem 0;
  font-size: 0.14rem;
  border: 0;
  color: #2A3350;
}
.Bind_Phone .inputDiv.codeDiv {
  background: white url("../../static/img/icon_code.png") no-repeat 0.16rem center;
  background-size: 0.18rem;
}
.Bind_Phone .inputDiv.codeDiv input {
  width: calc(100% - 1.3rem);
}
.Bind_Phone .inputDiv .code {
  color: #3672ED;
  padding: 0 0.13rem;
  height: 0.3rem;
  line-height: 0.3rem;
  border: 1px #3672ED solid;
  border-radius: 2px;
  position: absolute;
  right: 0.1rem;
  top: 50%;
  margin-top: -0.15rem;
  font-size: 0.13rem;
}
.Bind_Phone .blueBtn {
  margin-top: 0.35rem;
}



