#videoList {
  width: 100%;
  overflow: hidden;
}
#videoList::-webkit-scrollbar {
  display: none;
}
#videoList .videoUl {
  white-space: nowrap;
  overflow-x: auto;
  overflow-y: hidden;
  font-size: 0;
}
#videoList .videoUl::-webkit-scrollbar {
  display: none;
}
#videoList .videoUl .videoLi {
  display: inline-block;
  position: relative;
  height: 100%;
}
#videoList .videoUl .videoLi img {
  height: 1.2rem;
  width: auto;
  max-height: 100%;
  margin-left: 0.1rem;
  border-radius: 4px;
}
#videoList .videoUl .videoLi:last-child img {
  margin-right: 0.1rem;
}



