.teachDec {
  padding: 0 0.1rem 0.1rem;
}
.teachDec .title {
  font-size: 0.16rem;
  padding-top: 0.14rem;
}
.teachDec img {
  width: 0.35rem;
  height: 0.35rem;
  border-radius: 50%;
}
.teachDec .teach_item {
  padding-top: 0.15rem;
}
.teachDec .teach_item .userName {
  font-size: 0.14rem;
  padding-bottom: 0.07rem;
}
.teachDec .teach_item .text {
  font-size: 0.12rem;
  line-height: 1.6;
}
.teachDec .right {
  width: calc(100% - .35rem);
  padding-left: 0.1rem;
}



