#teacher .teacher_content {
  height: 100%;
}
#teacher .teacher_content .user_header {
  height: 1.8rem;
  background: url("../../static/img/icon_personBg.png") no-repeat;
  background-size: 100% 100%;
  position: relative;
  padding-top: 0.43rem;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
#teacher .teacher_content .user_header img {
  width: 0.66rem;
  height: 0.66rem;
  border: 5px rgba(255, 255, 255, 0.44) solid;
  border-radius: 50%;
  display: block;
  margin: 0 auto;
}
#teacher .teacher_content .user_header .textCont {
  color: white;
  text-align: center;
  line-height: 1;
}
#teacher .teacher_content .user_header .textCont .text_hidden {
  font-size: 0.15rem;
  margin: 0.08rem 0 0.02rem;
}
#teacher .teacher_content .user_header .textCont .idNUm {
  font-size: 0.14rem;
}
@media screen and (min-width: 768px) {
  #teacher .teacher_content .user_header {
    height: 3rem;
    padding-top: 1rem;
  }
}



