.user_list {
  padding: 0 0.1rem;
  background-color: white;
}
.user_list .user_list_item {
  height: 0.46rem;
  min-height: auto;
}
.user_list .user_list_item.order img {
  width: 0.15rem;
  height: auto;
}
.user_list .user_list_item.collect img {
  width: 0.18rem;
  height: auto;
}
.user_list .user_list_item.bind img {
  width: 0.13rem;
  height: auto;
}
.user_list .user_list_item.overMoney img {
  width: 0.17rem;
  height: auto;
}
.user_list .user_list_item.overMoney .am-list-line .am-list-extra {
  color: #f00;
}
.user_list .user_list_item.setting img {
  width: 0.18rem;
  height: auto;
}
.user_list .am-list-item {
  padding-left: 0.1rem;
}
.user_list .am-list-item.collect {
  padding-left: 0.08rem;
}
.user_list .am-list-item.collect .am-list-line {
  padding-left: 0.02rem !important;
}
.user_list .am-list-item::after {
  content: '';
  position: absolute;
  background-color: #EDEFF9;
  display: block;
  z-index: 1;
  top: auto;
  right: auto;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 1PX;
  -webkit-transform-origin: 0 0;
  -ms-transform-origin: 0 0;
  transform-origin: 0 0;
  -webkit-transform: scaleY(0.5);
  -ms-transform: scaleY(0.5);
  transform: scaleY(0.5);
}
.user_list .am-list-item.setting::after {
  display: none;
}
.user_list .am-list-body {
  border: 0 !important;
}
.user_list .am-list-line::after,
.user_list .am-list-body::before,
.user_list .am-list-body::after {
  display: none !important;
}
.user_list .am-list-item .am-list-line .am-list-content {
  color: #2A3350;
  font-size: 0.15rem;
  line-height: 1;
  padding: 0 !important;
}
.user_list .am-list-item .am-list-line .am-list-extra {
  font-size: 0.14rem;
  color: #999;
}
.user_list .am-list-item .am-list-line {
  padding-right: 0.1rem;
  border: 0 !important;
}
.user_list .am-list-item .am-list-line .am-list-arrow {
  background-image: url("../../../static/img/icon_next1.png");
  background-size: 100%;
  background-position: 100% 50%;
  width: 0.13rem;
  height: 0.13rem;
}
.user_list .am-list-item .am-list-thumb:first-child {
  width: 0.3rem;
  margin-right: 0;
}



