.detil-tab-item.courseDetail .whiteBg {
  background-color: #fff;
  padding: 0 0.1rem;
}
.detil-tab-item.courseDetail .title {
  font-size: 0.16rem;
  padding-top: 0.14rem;
}
.detil-tab-item.courseDetail .topCont {
  padding: 0.12rem 0.11rem;
  font-size: 0.13rem;
}
.detil-tab-item.courseDetail .topCont > div {
  padding-bottom: 0.12rem;
  line-height: 1;
}
.detil-tab-item.courseDetail .topCont > div span {
  padding-right: 0.2rem;
}
.detil-tab-item.courseDetail .topCont > div .studyNum {
  position: relative;
  margin-right: 0.2rem;
}
.detil-tab-item.courseDetail .topCont > div .studyNum::after {
  content: "";
  height: 80%;
  width: 1px;
  position: absolute;
  left: auto;
  right: 0;
  top: 50%;
  bottom: auto;
  background-color: #7A809B;
  -webkit-transform: translateY(-50%);
  -ms-transform: translateY(-50%);
  transform: translateY(-50%);
}
.detil-tab-item.courseDetail .topCont > div:last-child {
  padding-bottom: 0;
}
.detil-tab-item.courseDetail .teachDec {
  padding-bottom: 0.1rem;
}
.detil-tab-item.courseDetail .teachDec img {
  width: 0.35rem;
  height: 0.35rem;
  border-radius: 50%;
}
.detil-tab-item.courseDetail .teachDec .teach_item {
  padding-top: 0.15rem;
}
.detil-tab-item.courseDetail .teachDec .teach_item .userName {
  font-size: 0.14rem;
  padding-bottom: 0.07rem;
}
.detil-tab-item.courseDetail .teachDec .teach_item .text {
  font-size: 0.12rem;
  line-height: 1.6;
}
.detil-tab-item.courseDetail .teachDec .right {
  width: calc(100% - .35rem);
  padding-left: 0.1rem;
}
.detil-tab-item.courseDetail .courseDec {
  padding-bottom: 0.26rem;
}
.detil-tab-item.courseDetail .courseDec .text {
  padding-top: 0.07rem;
  line-height: 1.6;
}
.detil-tab-item.courseList .line_public {
  padding: 0.12rem 0 0.1rem;
}
.detil-tab-item.courseList .line_public:last-child::after {
  display: none;
}
.detil-tab-item.courseList .num {
  padding-left: 0.1rem;
  width: 0.4rem;
  font-size: 0.14rem;
  line-height: 1.2;
}
.detil-tab-item.courseList .textCont {
  width: calc(100% - .5rem);
}
.detil-tab-item.courseList .textCont .title_color {
  font-size: 0.14rem;
  padding-bottom: 0.1rem;
}
.detil-tab-item.courseList .status {
  float: right;
}
.detil-tab-item.courseList .status.icon_record {
  width: 0.15rem;
  height: 0.15rem;
  background: url("../../../static/img/icon_record.png") no-repeat left top;
  background-size: 0.15rem;
}
.detil-tab-item.courseList .status.icon_live {
  width: 0.15rem;
  height: 0.15rem;
  background: url("../../../static/img/icon_live.png") no-repeat left top;
  background-size: 0.15rem;
}
.detil-tab-item.courseList .status.icon_playBack {
  width: 0.15rem;
  height: 0.15rem;
  background: url("../../../static/img/icon_playBack.png") no-repeat left top;
  background-size: 0.15rem;
}
.detil-tab-item .core {
  padding: 0.1rem;
  text-align: center;
  border-bottom: 0.1rem #F3F6F9 solid;
}
.detil-tab-item .core .star {
  line-height: 1.2;
  position: relative;
  margin-bottom: 0.1rem;
}
.detil-tab-item .core .star .title_color {
  font-size: 0.18rem;
  padding-right: 0.05rem;
}
.detil-tab-item .core .star .iconfont {
  font-size: 0.2rem;
  color: #FFA734;
  padding-left: 0.05rem;
}
.detil-tab-item .core .num {
  font-size: 0.1rem;
}
.detil-tab-item .core .editorBtn {
  font-size: 0.12rem;
  background: url("../../../static/img/icon_next .png") no-repeat right center;
  background-size: 0.06rem;
  padding-right: 0.12rem;
  position: absolute;
  right: 0;
  top: 50%;
  -webkit-transform: translateY(-50%);
  -ms-transform: translateY(-50%);
  transform: translateY(-50%);
}



