#detil {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 100;
  height: 100%;
  overflow: hidden;
}
#detil .detil_content {
  height: calc(100% - .45rem);
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  overflow-y: scroll;
  -webkit-overflow-scrolling: touch;
  background-color: #F3F6F9;
  position: relative;
}
#detil .detil_content .imgDiv {
  width: 100%;
  height: 2rem;
  position: relative;
}
#detil .detil_content .imgDiv img {
  width: 100%;
  height: 100%;
  display: block;
  object-fit: cover;
  -o-object-fit: cover;
}
#detil .detil-tab.borderGray #comment_tab .load_more {
  padding-bottom: 0;
}
#detil .detil_content_bottom {
  position: absolute;
  bottom: 0;
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  height: 0.45rem;
  line-height: 0.45rem;
  background-color: #fff;
  z-index: 101;
}
#detil .detil_content_bottom .detil_content_bottom_left {
  width: calc(100% - 1.6rem);
  padding-left: 0.1rem;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  -webkit-box-shadow: 0 0px 2px rgba(0, 0, 0, 0.1);
          box-shadow: 0 0px 2px rgba(0, 0, 0, 0.1);
}
#detil .detil_content_bottom .detil_content_bottom_left .price {
  color: #4263E2;
  font-size: 0.16rem;
  letter-spacing: 1px;
}
#detil .detil_content_bottom .detil_content_bottom_left .price span {
  font-size: 0.12rem;
}
#detil .detil_content_bottom .detil_content_bottom_left .free {
  color: #06BC71;
  font-size: 0.15rem;
}
#detil .detil_content_bottom .detil_content_bottom_left .personBuy {
  float: right;
  padding-right: 0.12rem;
}
#detil .detil_content_bottom .detil_content_bottom_right {
  background-color: #3672ED;
  width: 1.6rem;
  line-height: 0.45rem;
  text-align: center;
  color: #FFFFFF;
  font-size: 0.16rem;
}



