.list_item {
  margin: 0.1rem;
  display: -ms-flexbox;
  display: flex;
  height: 1rem;
  overflow: hidden;
  background-color: #fff;
  border-radius: 2px;
}
.list_item .imgCont {
  position: relative;
  width: 50%;
}
.list_item .imgCont > img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  -o-object-fit: cover;
  overflow: hidden;
}
.list_item .imgCont .buyPerson {
  line-height: 0.18rem;
  background: rgba(0, 0, 0, 0.4);
  color: white;
  padding: 0 0.08rem;
  font-size: 0.1rem;
  position: absolute;
  bottom: 0;
  left: 0;
  max-width: 100%;
}
.list_item .rightCont {
  width: 50%;
  padding: 0.08rem 0.1rem;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  -ms-flex-flow: column;
      flex-flow: column;
  -moz-justify-content: space-between;
  -ms-justify-content: space-between;
  -o-justify-content: space-between;
  -khtml-justify-content: space-between;
  -ms-flex-pack: justify;
      justify-content: space-between;
}
.list_item .rightCont .title_color {
  font-size: 0.14rem;
}
.list_item .rightCont .name span {
  padding-right: 0.09rem;
  line-height: 1.2;
}
.list_item .rightCont .timeCont .progress {
  float: right;
  color: #3AB669;
}
.list_item .rightCont .timeCont .price {
  color: #444E6F;
  font-size: 0.14rem;
  width: 0.45rem;
  text-align: right;
  float: right;
}
.list_item .rightCont .timeCont .price span {
  font-size: 0.1rem;
  padding-right: 0.01rem;
}
.list_item .rightCont .timeCont .free {
  float: right;
  color: #06BC71;
  font-size: 0.13rem;
  width: 0.45rem;
  text-align: right;
}
.list_item .rightCont .icon_live {
  background: url("../../../static/img/icon_livePlay.gif") no-repeat left top;
  background-size: 0.14rem;
  font-size: 0.12rem;
  color: #3A9BFC;
  padding-left: 0.18rem;
  line-height: 0.13rem;
}
@media screen and (min-width: 768px) {
  .list_item {
    height: 1.1rem;
  }
  .list_item .imgCont {
    width: 40%;
  }
  .list_item .rightCont {
    width: 60%;
    padding: 0.1rem 0.1rem;
  }
}



