#home-category {
  width: 100%;
  color: #2A3350;
  font-size: 0.12rem;
}
#home-category .slider-container {
  height: 1.4rem;
  position: relative;
  overflow: hidden;
}
#home-category .slider-container .slider-nav {
  display: block;
  width: 100%;
  height: 100%;
}
#home-category .slider-container .slider-nav img {
  object-fit: cover;
  -o-object-fit: cover;
}
#home-category .slider-container .slider-nav .swiper-pagination-bullet-active {
  background-color: #DDDDDD;
}



