.my_balance {
  height: 100%;
}
.my_balance .balance_content {
  height: 100%;
  padding-top: 0.58rem;
}
.my_balance .balance_content.whiteBg {
  background-color: white;
}
.my_balance .balance_content .am-tabs-tab-bar-wrap {
  position: relative;
}
.my_balance .balance_content .am-tabs-tab-bar-wrap::before {
  content: '';
  position: absolute;
  background-color: #EDEFF9;
  display: block;
  z-index: 1;
  top: 0;
  right: auto;
  bottom: auto;
  left: 0;
  width: 100%;
  height: 1PX;
  -webkit-transform-origin: 0 0;
  -ms-transform-origin: 0 0;
  transform-origin: 0 0;
  -webkit-transform: scaleY(0.5);
  -ms-transform: scaleY(0.5);
  transform: scaleY(0.5);
}
.my_balance .balance_content .am-tabs-tab-bar-wrap::after {
  content: '';
  position: absolute;
  background-color: #EDEFF9;
  display: block;
  z-index: 1;
  top: auto;
  right: auto;
  bottom: -1px;
  left: 0;
  width: 100%;
  height: 1PX;
  -webkit-transform-origin: 0 0;
  -ms-transform-origin: 0 0;
  transform-origin: 0 0;
  -webkit-transform: scaleY(0.5);
  -ms-transform: scaleY(0.5);
  transform: scaleY(0.5);
}
.my_balance .balance_content .am-tabs-tab-bar-wrap .am-tabs-default-bar-tab:first-child::after {
  display: block !important;
  content: "";
  height: 55%;
  width: 1px;
  position: absolute;
  left: auto!important;
  right: 0!important;
  top: 50%!important;
  bottom: auto!important;
  background-color: #EDEFF9 !important;
  -webkit-transform: translateY(-50%);
  -ms-transform: translateY(-50%);
  transform: translateY(-50%);
}
.my_balance .balance_content #record_list {
  margin-top: 0.1rem;
}
.my_balance .balance_content .account {
  height: 1.13rem;
  margin: 0.3rem 0.1rem 0.33rem;
  padding: 0.15rem 0.24rem;
  font-size: 0.16rem;
  background: url("../../static/img/icon_myBalance.png") no-repeat;
  background-size: cover;
  border-radius: 2px;
}
@media screen and (min-width: 768px) {
  .my_balance .balance_content .account {
    height: 1.3rem;
  }
}
.my_balance .balance_content .account .title_color {
  font-size: 0.44rem;
  line-height: 1;
}
.my_balance .balance_content .account .title_color span {
  font-size: 0.16rem;
  padding-left: 0.1rem;
  vertical-align: middle;
}
.my_balance .balance_content .recharge_btn {
  margin: 0 0.1rem;
}



