.search_header {
  height: 0.44rem;
  background-color: #fff;
  padding: 0 0.45rem 0 0.45rem;
  position: relative;
}
.search_header .searchr-back {
  position: absolute;
  left: 0.1rem;
  top: 50%;
  -webkit-transform: translateY(-50%);
      -ms-transform: translateY(-50%);
          transform: translateY(-50%);
  font-size: 0.2rem;
  font-weight: bold;
  width: 0.3rem;
  height: 0.3rem;
  line-height: 0.3rem;
  color: #2A3350;
}
.search_header .search_btn {
  position: absolute;
  right: 0.1rem;
  top: 50%;
  -webkit-transform: translateY(-50%);
      -ms-transform: translateY(-50%);
          transform: translateY(-50%);
  font-size: 0.16rem;
  color: #3672ED;
}
.search_header .search_input {
  width: calc(100% - .1rem);
  padding: 0.14rem 0.15rem;
  height: 0.18rem;
  line-height: 0.18rem;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  outline: none;
  color: #7A809B;
  font-size: 0.14rem;
  border: 1px #EDEDED solid !important;
  border-radius: 2px;
  margin-top: 0.08rem;
  margin-bottom: 0.08rem;
  -webkit-appearance: none;
}



