#home {
  height: 100%;
}
#home .bannerImg {
  object-fit: cover;
  -o-object-fit: cover;
}
#home .home_content {
  height: calc(100% - .64rem);
}
#home .home_content .same_title {
  font-size: 0.18rem;
  padding: 0.2rem 0 0.15rem;
}
#home .home_content .same_title.noTop {
  padding-top: 0.02rem;
}
#home .home_content .same_title.noBottom {
  padding-bottom: 0.05rem;
  position: relative;
}
#home .home_content .same_title.noBottom i {
  position: absolute;
  right: 0.1rem;
  top: 0.13rem;
  display: block;
  width: 0.3rem;
  height: 0.3rem;
  line-height: 0.3rem;
  color: #424657;
  font-size: 0.18rem;
}



