#commentBig {
  position: absolute;
  pointerEvents: 'none';
  top: 0;
  width: 100%;
  height: 100%;
  z-index: 111;
}
#commentBig .shade {
  height: 100%;
  background-color: rgba(0, 0, 0, 0.3);
}
#commentBig #comment {
  height: 4.3rem;
  width: 100%;
  background-color: white;
  position: absolute;
  bottom: -4.3rem;
  left: 0;
  z-index: 44;
  padding: 0 0.15rem;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
#commentBig #comment .icon_close {
  text-align: right;
  padding-top: 0.15rem;
}
#commentBig #comment .icon_close i {
  padding-top: 0.15rem;
  padding-left: 0.15rem;
  font-size: 0.15rem;
}
#commentBig #comment h2 {
  color: #2A3350;
  font-size: 0.18rem;
  font-weight: normal;
  text-align: center;
  margin-bottom: 0.25rem;
}
#commentBig #comment .content {
  height: calc(100% - 1.01rem);
}
#commentBig #comment .my_flex {
  margin-bottom: 0.25rem;
  -moz-align-items: center;
  -ms-align-items: center;
  -o-align-items: center;
  -khtml-align-items: center;
  -ms-flex-align: center;
      align-items: center;
}
#commentBig #comment .my_flex .title {
  font-size: 0.15rem;
  margin-right: 0.05rem;
}
#commentBig #comment .my_flex .star {
  line-height: 1.2;
  position: relative;
}
#commentBig #comment .my_flex .star .title_color {
  font-size: 0.18rem;
  padding-right: 0.05rem;
}
#commentBig #comment .my_flex .star .iconfont {
  font-size: 0.2rem;
  color: #FFA734;
  padding-right: 0.05rem;
}
#commentBig #comment .my_flex .star span {
  font-size: 0.1rem;
  padding-left: 0.12rem;
}
#commentBig #comment textarea {
  margin-bottom: 0.35rem;
  height: 1.3rem;
  background-color: #EFF3F7;
  border-radius: 2px;
  padding: 0.1rem 0.1rem;
  width: 100%;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  border: 0;
  resize: none;
  line-height: 1.4;
}
#commentBig #comment .selectCont {
  position: relative;
  height: 0.4rem;
  width: 1.5rem;
}
#commentBig #comment .select {
  border: 1px #EDEFF9 solid;
  border-radius: 2px;
  font-size: 0.13rem;
  color: #2A3350;
  padding: 0.1rem 0.15rem;
  width: 100%;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  position: relative;
}
#commentBig #comment .select::after {
  content: "";
  position: absolute;
  right: 0.1rem;
  top: 50%;
  width: 0.08rem;
  height: 0.08rem;
  border-top: 1px solid #7A809B;
  border-right: 1px solid #7A809B;
  -webkit-transform: translateY(-50%) rotate(135deg);
      -ms-transform: translateY(-50%) rotate(135deg);
          transform: translateY(-50%) rotate(135deg);
  margin-top: -2px;
}
#commentBig #comment .rightDiv ul {
  -webkit-box-shadow: 0 2px 3px rgba(0, 0, 0, 0.2);
          box-shadow: 0 2px 3px rgba(0, 0, 0, 0.2);
  border-top: 0;
  position: relative;
  top: 0.05rem;
  left: 0;
  z-index: 99;
  background: white;
  font-size: 0.13rem;
  width: 100%;
  color: #2A3350;
  max-height: 1.4rem;
}
#commentBig #comment .rightDiv ul li {
  margin: 0 0.15rem;
  padding: 0.1rem 0;
}
#commentBig #comment .rightDiv ul li:hover {
  color: #3672ED;
}
.comment_panel_enter {
  -webkit-animation-name: moveEnter;
          animation-name: moveEnter;
  -webkit-animation-duration: 0.15s;
          animation-duration: 0.15s;
  -webkit-animation-timing-function: linear;
          animation-timing-function: linear;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  /*设置动画的播放状态  paused:暂停   running:播放*/
  -webkit-animation-play-state: running;
          animation-play-state: running;
}
@-webkit-keyframes moveEnter {
  from {
    bottom: -4.3rem;
  }
  to {
    bottom: 0;
  }
}
@keyframes moveEnter {
  from {
    bottom: -4.3rem;
  }
  to {
    bottom: 0;
  }
}



