.item {
  width: 50%;
  padding: 0 0.05rem 0.1rem;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
.item .inner {
  border-radius: 2px;
  overflow: hidden;
}
.item a {
  display: block;
  border-radius: 4px;
}
.item .imgDiv {
  position: relative;
  height: 1rem;
}
.item .imgDiv img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  -o-object-fit: cover;
  display: block;
  overflow: hidden;
}
.item .imgDiv .sign {
  color: #fff;
  font-size: 0.1rem;
  padding: 0.04rem 0.09rem 0.02rem;
  background-color: rgba(0, 0, 0, 0.4);
  position: absolute;
  right: 0;
  bottom: 0;
}
.item .textCont {
  padding: 0.08rem 0.1rem;
  background-color: #fff;
  font-size: 0.14rem;
}
.item .textCont .tagDiv {
  margin: 0.05rem 0 0.07rem;
}
.item .textCont .time {
  font-size: 0.11rem;
  color: #9E9E9E;
  padding-bottom: 0.04rem;
  overflow: hidden;
}
.item .textCont .time span {
  float: left;
}
.item .textCont .time .courseNum {
  float: right;
  white-space: nowrap;
}
.item .textCont .time .courseNum i {
  font-size: 0;
}
.item .textCont .textBottom {
  margin-top: 0.06rem;
  height: 0.25rem;
  line-height: 0.25rem;
  overflow: hidden;
  -moz-align-items: center;
  -ms-align-items: center;
  -o-align-items: center;
  -khtml-align-items: center;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: justify;
      justify-content: space-between;
}
.item .textCont .textBottom .textLeft {
  display: block;
  width: calc(100% - 0.45rem);
}
.item .textCont .textBottom .free {
  color: #06BC71;
  font-size: 0.13rem;
  width: 0.45rem;
  text-align: right;
}
.item .textCont .textBottom .price {
  color: #444E6F;
  font-size: 0.14rem;
  width: 0.45rem;
  text-align: right;
}
.item .textCont .textBottom .price span {
  font-size: 0.1rem;
  padding-right: 0.01rem;
}
.item .textCont .textBottom .text_color {
  font-size: 0.13rem;
  line-height: 1.2;
  width: calc(100% - .7rem);
  padding-left: 0.05rem;
}
.item .textCont img {
  width: 0.25rem;
  height: 0.25rem;
  border-radius: 50%;
  vertical-align: middle;
  border: 0;
  overflow: hidden;
}
@media screen and (min-width: 768px) {
  .item .imgDiv {
    height: 1.4rem;
  }
}



