.place_order {
  height: 100%;
}
.place_order .place_orderDiv {
  height: 100%;
  position: relative;
  padding-top: 0.68rem;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
.place_order .place_orderDiv .sign_success {
  width: 100%;
  position: absolute;
  top: 25%;
  left: 0;
  text-align: center;
  font-size: 0.16rem;
  line-height: 1.2;
}
.place_order .place_orderDiv .sign_success i {
  font-size: 0.71rem;
  color: #21B360;
}
.place_order .place_orderDiv .sign_success .text {
  font-size: 0.2rem;
  color: #21B360;
  padding: 0.24rem 0 0.2rem;
}
.place_order .place_order_content {
  height: calc(100% - .45rem);
}
.place_order .place_order_content .orderMsg {
  margin-bottom: 0.2rem;
  background-color: white;
  padding: 0.1rem 0.1rem 0;
  color: #2A3350;
}
.place_order .place_order_content .orderMsg .title {
  font-size: 0.18rem;
  margin-bottom: 0.15rem;
}
.place_order .place_order_content .orderMsg .font12 {
  color: #7A809B;
}
.place_order .place_order_content .orderMsg .font12 span {
  padding: 0.1rem 0.05rem 0 0;
  display: inline-block;
}
.place_order .place_order_content .orderMsg .font14 > div {
  margin-bottom: 0.1rem;
}
.place_order .place_order_content .orderMsg .orderMoney {
  font-size: 0.14rem;
  padding: 0.15rem 0;
}
.place_order .place_order_content .orderMsg .orderMoney .price {
  font-size: 0.16rem;
  color: #4263E2;
}
.place_order .place_order_content .orderMsg .orderMoney .price span {
  font-size: 0.12rem;
}
.place_order .place_order_content .payWay {
  padding-top: 0.1rem;
  margin-bottom: 0.1rem;
}
.place_order .place_order_content .payWay .payTitle {
  font-size: 0.18rem;
  padding-left: 0.1rem;
  color: #2A3350;
}
.place_order .place_order_content .payWay #pay_list .payItem .title_color {
  color: #7A809B;
  font-size: 0.12rem;
}



