.home_header {
  background-color: #3672ED;
  text-align: center;
  padding: 0.1rem 0.13rem;
  margin-top: 0;
}
.home_header .div-input {
  background-color: #fff;
  font-size: 0.14rem;
  border-radius: 3px;
  color: #7A809B;
  width: 100%;
  height: 0.3rem;
  overflow: hidden;
}
.home_header .div-input > i {
  font-size: 0.13rem;
  margin-right: 0.05rem;
}
.home_header .div-input a {
  padding: 0.08rem 0;
}



