#public_header {
  z-index: 50;
  background-color: rgba(0, 0, 0, 0.5);
  position: absolute;
  width: 100%;
  text-align: center;
  font-size: 0.18rem;
}
#public_header.Bg {
  background-color: #fff;
}
#public_header.Bs {
  -webkit-box-shadow: 0 0.5px 1px rgba(210, 210, 210, 0.5);
          box-shadow: 0 0.5px 1px rgba(210, 210, 210, 0.5);
}
#public_header.lineB .headerCont {
  color: #2A3350;
}
#public_header .header-back {
  position: absolute;
  left: 0.1rem;
  width: 0.3rem;
  height: 0.3rem;
  line-height: 0.3rem;
  text-align: left;
  top: 50%;
  -webkit-transform: translateY(-50%);
      -ms-transform: translateY(-50%);
          transform: translateY(-50%);
}
#public_header .header-back .iconfont {
  font-size: 0.22rem;
  font-weight: bold;
}
#public_header .header-shaixuan {
  position: absolute;
  right: 0.1rem;
}
#public_header .header-shaixuan .iconfont {
  font-size: 0.22rem;
  font-weight: bold;
}
#public_header .header-jiaoyi {
  position: absolute;
  right: 0.1rem;
  font-size: 0.14rem;
  top: 50%;
  -webkit-transform: translateY(-50%);
  -ms-transform: translateY(-50%);
  transform: translateY(-50%);
}
#public_header .header-sousuo {
  position: absolute;
  right: 0.1rem;
  font-size: 0.14rem;
  top: 50%;
  -webkit-transform: translateY(-50%);
      -ms-transform: translateY(-50%);
          transform: translateY(-50%);
}
#public_header .header-title {
  overflow: hidden;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
  white-space: nowrap;
  width: calc(100% - 1.05rem);
  margin: 0 auto;
}



