#orderListItem {
  background-color: white;
  padding: 0.15rem 0.15rem 0.12rem;
  margin-bottom: 0.1rem;
}
#orderListItem .orderNum {
  font-size: 0.13rem;
  overflow: hidden;
  padding-bottom: 0.12rem;
}
#orderListItem .orderNum .time {
  float: right;
}
#orderListItem .title_color {
  font-size: 0.16rem;
  line-height: 0.22rem;
  margin: 0.1rem 0 0.06rem;
}
#orderListItem .bottom {
  overflow: hidden;
  line-height: 0.2rem;
}
#orderListItem .bottom .teacherName {
  font-size: 0.13rem;
  width: calc(100% - .6rem);
  display: inline-block;
  vertical-align: middle;
}
#orderListItem .bottom .price {
  color: #3672ED;
  font-size: 0.18rem;
  float: right;
}
#orderListItem .bottom .price span {
  font-size: 0.12rem;
  vertical-align: text-bottom;
}
#orderListItem .bottom .free {
  float: right;
  color: #06BC71;
  font-size: 0.17rem;
}



