#comment_list_item {
  background-color: white;
}
#comment_list_item:last-child {
  border-bottom: 0.1rem #F3F6F9 solid;
}
#comment_list_item:last-child .item::after {
  display: none;
}
#comment_list_item .item {
  padding: 0.1rem;
  width: 100%;
}
#comment_list_item .item img {
  width: 0.35rem;
  height: 0.35rem;
  border-radius: 50%;
  margin-right: 0.1rem;
}
#comment_list_item .item .title_color {
  font-size: 0.14rem;
  display: inline-block;
  width: calc(100% - 1.2rem);
}
#comment_list_item .item .rightCont {
  width: calc(100% - .45rem);
}
#comment_list_item .item .content {
  font-size: 0.12rem;
  margin-top: 0.07rem;
  word-break: break-word;
  line-height: 1.6;
}
#comment_list_item .item .userName {
  line-height: 1.2;
  position: relative;
  padding-top: 0.05rem;
}
#comment_list_item .item .time {
  font-size: 0.12rem;
  position: absolute;
  right: 0;
  top: 50%;
  -webkit-transform: translateY(-50%);
  -ms-transform: translateY(-50%);
  transform: translateY(-50%);
}



