#header {
  position: fixed;
  width: 100%;
  color: black;
  text-align: center;
  font-size: 0.18rem;
  z-index: 50;
  background: -webkit-linear-gradient(rgba(0, 0, 0, 0.8), rgba(0, 0, 0, 0));
  /* Safari 5.1 - 6.0 */
  background: -o-linear-gradient(rgba(0, 0, 0, 0.8), rgba(0, 0, 0, 0));
  /* Opera 11.1 - 12.0 */
  /* Firefox 3.6 - 15 */
  background: -webkit-gradient(linear, left top, left bottom, from(rgba(0, 0, 0, 0.8)), to(rgba(0, 0, 0, 0)));
  background: linear-gradient(rgba(0, 0, 0, 0.8), rgba(0, 0, 0, 0));
  /* 标准的语法 */
}
#header.blueBg {
  background-color: #3672ED;
}
#header .header-back {
  position: absolute;
  left: 0.15rem;
  color: #FFFFFF;
  top: 50%;
  -webkit-transform: translateY(-50%);
      -ms-transform: translateY(-50%);
          transform: translateY(-50%);
  width: 0.3rem;
  height: 0.3rem;
  line-height: 0.3rem;
  text-align: left;
}
#header .header-back .iconfont {
  font-size: 0.22rem;
  font-weight: bold;
}
#header .header-collect {
  position: absolute;
  right: 0.15rem;
  top: 50%;
  -webkit-transform: translateY(-50%);
      -ms-transform: translateY(-50%);
          transform: translateY(-50%);
}
#header .header-collect .iconfont {
  font-size: 0.17rem;
  color: #FFFFFF;
  font-weight: bold;
}
#header .header-title {
  color: #FFFFFF;
  overflow: hidden;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
  white-space: nowrap;
  width: calc(100% - 1.05rem);
  margin: 0 auto;
}



