.orderListItem {
  background-color: white;
  padding: 0.1rem;
  font-size: 0.14rem;
  position: relative;
}
.orderListItem:last-child::after {
  display: none;
}
.orderListItem .text_hidden {
  margin-bottom: 7px;
  width: calc(100% - 80px);
  line-height: 1.4;
}
.orderListItem .moneyRecording {
  position: absolute;
  right: 10px;
  top: 50%;
  -webkit-transform: translateY(-50%);
  -ms-transform: translateY(-50%);
  transform: translateY(-50%);
}



